import React from 'react';
import { Link } from 'react-router-dom';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './nav.css';

import $ from 'jquery';

import { DiReact } from "react-icons/di";

import logo from '../logo.svg';


const mobileNavUp = (e) => {
    
    //alert('alert');
    
    //$(".navbar-toggler").attr('class','navbar-toggler collapsed');  // navbar-toggler -> navbar-toggler collapsed 클래스명 변경
    //document.getElementById("responsive-navbar-nav").className = "navbar-collapse collapse";      // id 로 접근하여, 클래스명 변경
    
    if(window.innerWidth <= 991)        // 브라우저 가로 991px 이하면, 모바일
    {
        $('.navbar-toggler').click();       // 상단 Nav바에서, 모바일 버전 햄버거 버튼 클릭
    }
}


function Navigation() {

    return (
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
            {/*  <img src={logo} className="App-logo-nav" alt="logo" />  */}
           
            <Navbar.Brand href="/">
                <img src={logo} className="App-logo-nav" alt="logo" />
                {/* Navbar */}
                dpround
            </Navbar.Brand>
            
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                    {/*
                    <Nav.Link href="#Home">Home</Nav.Link>
                    <Nav.Link href="/About">About1</Nav.Link>
                    <Nav.Link className="nav-link" to="/About">About2</Nav.Link>

                    <Nav.Link href="#pricing">Pricing</Nav.Link>

                    <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown>
                    */}  
                    {/* 
                        Nav.Link href='/About'으로 하면, 페이지가 전체가 새로고침됨. 
                        Link to 로 하면, 해당 컴포넌트만 변경됨. 
                        Link to 로 했더니, 메뉴 클릭하면, 햄버거 버튼이 접어지지 않아서, onClick 이벤트 추가함.
                    */}   
                    {/*  <Link className="nav-link" to='/home' onClick={mobileNavUp}>Home</Link>  */}
                    <Link className="nav-link" to='/about' onClick={mobileNavUp}>About</Link>
                    <Link className="nav-link" to='/page' onClick={mobileNavUp}>Gallery</Link>

                    <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
                        {/* 
                            navDropdown도 마찬가지로, 페이지 전체가 새로고침됨.
                            Link to 로 하면, 해당 컴포넌트만 변경됨.
                            Link to 로 했더니, 메뉴 클릭하면, 햄버거 버튼이 접어지지 않아서, onClick 이벤트 추가함.
                        */}
                        <NavDropdown.Item as={Link} to="/page1-1" onClick={mobileNavUp}>Page1-1</NavDropdown.Item>
                        <NavDropdown.Item as={Link} to="/page1-2" onClick={mobileNavUp}>Page1-2</NavDropdown.Item> 
                        <NavDropdown.Item as={Link} to="/page1-3" onClick={mobileNavUp}>Page1-3</NavDropdown.Item> 
                        <NavDropdown.Divider />
                        <NavDropdown.Item as={Link} to="/page2-1" onClick={mobileNavUp}>Page2-1</NavDropdown.Item> 
                        <NavDropdown.Item as={Link} to="/page2-2" onClick={mobileNavUp}>Page2-2</NavDropdown.Item> 
                    </NavDropdown>
                </Nav>

                <Nav>
                    <Link className="nav-link" to='/info' onClick={mobileNavUp}>Info</Link>   
                    <Link className="nav-link" to='/user' onClick={mobileNavUp}>User</Link>   
                </Nav>

                {/*
                <Form inline>
                    <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                    <Button variant="outline-info">Search</Button>
                </Form>
                */}
            </Navbar.Collapse>
            
        </Navbar>
    );
}

export default Navigation;
