import React, { useState } from 'react';

const Page1_1 = () => {
  const [weatherData, setWeatherData] = useState(null);

 const handleButtonClick = () => {
    // 요청 데이터
    const requestData = {
      location: 'Seoul',
      year: 2024
    };

    // fetch('http://per.iptime.org:5000/api/weather', {
    fetch('http://per.iptime.org:5000/api/weather', {
    method: 'POST',
      mode: 'no-cors', // CORS 정책 무시
      headers: {
        'Content-Type': 'application/json' // JSON 형식으로 요청
      },
      body: JSON.stringify(requestData) // 요청 데이터를 JSON 문자열로 변환하여 본문에 포함
    })
    .then(response => response.json())
    .then(data => {
      // API 응답을 상태에 설정
      setWeatherData(data);
    })
    .catch(error => {
      console.error('Error fetching weather data:', error);
    });
  };

  
  return (
    <div>
      <h1>Page1-1</h1>
      <button onClick={handleButtonClick}>Get Weather Data</button>
      {weatherData && (
        <div>
          <h2>Weather Data:</h2>
          <p>Location: {weatherData.location}</p>
          <p>Year: {weatherData.year}</p>
          <p>Temperature: {weatherData.temperature}</p>
          {/* 기타 필요한 정보들을 화면에 출력 */}
        </div>
      )}
    </div>
  );
};

export default Page1_1;
